<template>
  <div id="app" class="container">
    <h2>HappyHouse Qna</h2>
    <header id="index_header" class="jumbotron text-center mb-1"></header>
    <table border="1" class="table table-hover" style="margin-top:10px;">
      <tr id="qna_title">
        <th colspan="2" style="color:white;">Q&A</th>
      </tr>
      <tr>
        <td>번호</td>
        <td>{{ item.qid }}</td>
      </tr>
      <tr>
        <td>제목</td>
        <td>
          {{ item.qtitle }}
        </td>
      </tr>
      <tr>
        <td>내용</td>
        <td>
          {{ item.qcontent }}
        </td>
      </tr>
      <tr>
        <td>작성자</td>
        <td>{{ item.qwriter }}</td>
      </tr>
      <tr>
        <td>일시</td>
        <td>{{ item.qdate }}</td>
      </tr>
      <tr>
        <td>조회수</td>
        <td>{{ item.qviews }}</td>
      </tr>
    </table>

    <br />
    <br />

    <div style="text-align:left;">
      <h5>댓글</h5>
      <table>
        <tr v-for="c in citem" v-bind:key="c.qcid">
          <td>{{ c.qcwriter }} :</td>
          <td>{{ c.qccontent }}</td>
          <td>{{ c.qcdate }}</td>
        </tr>
      </table>
    </div>
    <br />
    <br />
    <div>
      <table>
        <tr>
          <td>
            댓글 :
          </td>
          <td>
            <textarea v-model="newItem.qctitle" />
          </td>
          <td>
            <textarea v-model="newItem.qccontent" />
          </td>
          <button type="button" class="btn btn-secondary" @click="commentadd">
            댓글
          </button>
        </tr>
      </table>
    </div>
    <div>
      <button type="button" class="btn btn-danger" @click="remove">삭제</button>
      <button type="button" class="btn btn-info" v-on:click="list">
        목록
      </button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      item: [],
      citem: [],
      newItem: {
        qctitle: '',
        qid: '',
        qccontent: '',
      },
      qid: '',
    };
  },
  created() {
    this.qid = this.$route.params.qid;
    axios
      .get('http://localhost:7777/qna/search?qid=' + this.qid)
      .then((response) => {
        console.log(response);
        this.item = response.data;
      })
      .catch()
      .finally();
    axios
      .get('http://localhost:7777/qna/commentlist?qid=' + this.qid)
      .then((response) => {
        console.log(response);
        this.citem = response.data;
      })
      .catch()
      .finally();
  },
  methods: {
    remove: function() {
      axios
        .delete('http://localhost:7777/qna/delete/' + this.qid)
        .then((response) => {
          console.log(response);
          alert('삭제하였습니다.');
          this.$router.push('/qna/qnalist');
        })
        .catch()
        .finally();
    },
    modi: function() {
      axios
        .put('http://localhost:7777/qna/update', this.item)
        .then((response) => {
          console.log(response);
          alert('수정하였습니다.');
          this.$router.push('/qna/qnalist');
        })
        .catch()
        .finally();
    },
    commentadd: function() {
      this.newItem.qid = this.qid;
      axios
        .post('http://localhost:7777/qna/addcomment', this.newItem)
        .then((response) => {
          console.log(response);
          alert('댓글을 등록하였습니다.');
          this.$router.push('/qna/qnadetail/' + this.qid);
        })
        .catch()
        .finally();
    },
    list: function() {
      this.$router.push('/qna/qnalist');
    },
  },
};
</script>

<style></style>
