import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../components/QnaList.vue';
import QnaList from '../components/QnaList.vue';
import QnaAdd from '../components/QnaAdd.vue';
import QnaDetail from '../components/QnaDetail.vue';
import Qna from '../views/Qna.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/about',
    name: 'About',
  },
  {
    path: '/qna',
    name: 'Qna',
    component: Qna,
    redirect: 'qna/qnalist',
    children: [
      {
        path: 'qnalist',
        name: 'QnaList',
        component: QnaList,
      },
      {
        path: 'qnaadd',
        name: 'QnaAdd',
        component: QnaAdd,
      },
      {
        path: 'qnadetail/:qid',
        name: 'QnaDetail',
        component: QnaDetail,
      },
    ],
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
