<script src="https://ajax.googleapis.com/ajax/libs/jquery/3.5.1/jquery.min.js"></script>
<script src="https://cdnjs.cloudflare.com/ajax/libs/popper.js/1.16.0/umd/popper.min.js"></script>
<script src="https://maxcdn.bootstrapcdn.com/bootstrap/4.5.2/js/bootstrap.min.js"></script>
<style>
html,
body {
  height: 100%;
}
.wrapper {
  height: 85%;
}

.btn-primary {
  margin-left: 20px;
}

h2 {
  color: white;
  font-weight: bold;
}

h1 {
  font-size: 50px;
  color: black;
}

h3 {
  color: black;
  font-weight: bold;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.error {
  color: red;
}
</style>
<template>
  <div id="app">
    <h1>HappyHouse Qna 등록</h1>
    <div class="form-group" align="left">
      <label for="qtitle" style="display: block">제목:</label>
      <input
        type="text"
        class="form-control"
        id="qtitle"
        name="qtitle"
        placeholder="제목을 입력하세요."
        required
        v-model="newQna.qtitle"
      />
    </div>
    <div class="form-group" align="left">
      <label for="qcontent" style="display: block">내용:</label>
      <textarea
        class="form-control"
        id="qcontent"
        rows="15"
        cols="50"
        name="qcontent"
        placeholder="내용을 입력하세요."
        v-model="newQna.qcontent"
      ></textarea>
    </div>
    <div style="text-align: right; display: block">
      <button type="submit" class="btn btn-primary" @click="add">등록</button>
      <button type="cancel" class="btn btn-warning">
        <router-link :to="{ name: 'QnaList' }">취소</router-link>
      </button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      newQna: {
        qtitle: '',
        qcontent: '',
      },
    };
  },
  methods: {
    add() {
      axios
        .post('http://localhost:7777/qna/insert', this.newQna)
        .then((response) => {
          alert('등록되었습니다.');
          this.$router.push('/qna/qnalist');
        })
        .catch()
        .finally();
    },
  },
};
</script>
