<template>
  <div class="container">
    <header id="index_header" class="jumbotron text-center mb-1"></header>
    <div style="text-align:right;">
      <button type="button" class="btn btn-info" v-on:click="add">
        Q&A
      </button>
    </div>
    <input type="text" v-model="keyword" />
    <button class="btn btn-success" v-on:click="search(keyword)">검색</button>
    <table border="1" class="table table-hover" style="margin-top:10px;">
      <tr id="qna_title">
        <th colspan="6" style="color:white;">Q&A</th>
      </tr>
      <tr>
        <th>번호</th>
        <th>제목</th>
        <th>내용</th>
        <th>작성자</th>
        <th>일시</th>
        <th>조회수</th>
      </tr>
      <tr v-for="item in items" v-bind:key="item.qid">
        <td>{{ item.qid }}</td>
        <td>
          <router-link :to="'/qna/qnadetail/' + item.qid">{{
            item.qtitle
          }}</router-link>
        </td>
        <td>{{ item.qcontent }}</td>
        <td>{{ item.qwriter }}</td>
        <td>{{ item.qdate }}</td>
        <td>{{ item.qviews }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      items: [],
      keyword: '',
    };
  },
  created() {
    axios
      .get('http://localhost:7777/qna/searchAll')
      .then((response) => {
        console.log(response);
        this.items = response.data;
      })
      .catch()
      .finally();
  },
  methods: {
    add: function() {
      this.$router.push('/qna/qnaadd');
    },
    search: function(keyword) {
      console.log(keyword);
      axios
        .get(
          'http://localhost:7777/house/qna/searchname?qtitle=' + this.keyword
        )
        .then((response) => {
          console.log(response);
          this.items = response.data;
        })
        .catch()
        .finally();
    },
  },
};
</script>
